.form {
  padding-bottom: 32px;
}

@media (min-width: @tablet-width) {
  .form {
    padding-bottom: 60px;

    .wrapper {
      padding: 0 70px;
    }
  }
}

@media (min-width: @desktop-width) {
  .form {
    padding-bottom: 80px;

    .wrapper {
      padding: 0 110px;
    }
  }
}

.form__description {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 39px;
  padding: 0 20px;
}

@media (min-width: @tablet-width) {
  .form__description {
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 66px;
    padding-left: 70px;
  }
}

@media (min-width: @desktop-width) {
  .form__description {
    padding-left: 110px;
    max-width: 1440px;
    margin: 0 auto 66px auto;
  }
}
